<template>
    <div v-if="!isBrowserSupported">
        <div class="px-4 py-8 bg-white shadow sm:rounded-lg sm:px-10">
            <h2 class="text-xl font-bold text-center">Browser not supported</h2>
            <p class="mt-4 text-center">
                WebAuthn is not supported in this browser. Please use a modern
                browser like Chrome, Firefox, or Safari.
            </p>
        </div>
    </div>
    <div v-else class="px-4 py-8 bg-white shadow sm:rounded-lg sm:px-10">
        <form class="space-y-6" @submit.prevent="submitLogin">
            <h2 class="text-xl font-bold text-center">Log In</h2>

            <div>
                <label
                    for="username"
                    class="block text-sm font-medium text-gray-700"
                    >Username</label
                >
                <div class="relative mt-1 rounded-md shadow-sm">
                    <input
                        type="text"
                        id="username"
                        v-model="username"
                        autocomplete="off"
                        required
                        autocapitalize="off"
                        class="block w-full px-3 py-2 placeholder-gray-400 border rounded-md appearance-none focus:outline-none sm:text-sm"
                        :class="
                            errors.username
                                ? 'border-red-300 focus:border-red-500 focus:ring-red-500'
                                : 'border-gray-300 focus:border-sky-500 focus:ring-sky-500'
                        "
                    />

                    <div
                        v-if="errors.username"
                        class="absolute inset-y-0 right-0 flex items-center pr-3 pointer-events-none"
                    >
                        <svg
                            class="w-5 h-5 text-red-500"
                            xmlns="http://www.w3.org/2000/svg"
                            viewBox="0 0 20 20"
                            fill="currentColor"
                            aria-hidden="true"
                        >
                            <path
                                fill-rule="evenodd"
                                d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-8-5a.75.75 0 01.75.75v4.5a.75.75 0 01-1.5 0v-4.5A.75.75 0 0110 5zm0 10a1 1 0 100-2 1 1 0 000 2z"
                                clip-rule="evenodd"
                            />
                        </svg>
                    </div>
                </div>
            </div>

            <div>
                <!-- <button
                    type="submit"
                    class="flex justify-center w-full px-4 py-2 text-sm font-medium text-white border border-transparent rounded-md shadow-sm bg-sky-600 hover:bg-sky-700 focus:outline-none focus:ring-2 focus:ring-sky-500 focus:ring-offset-2"
                >
                    Register with Passkey
                </button> -->

                <button
                    type="submit"
                    class="mt-3 flex justify-center w-full px-4 py-2 text-sm font-medium text-white border border-transparent rounded-md shadow-sm bg-sky-600 hover:bg-sky-700 focus:outline-none focus:ring-2 focus:ring-sky-500 focus:ring-offset-2"
                >
                    Login with Passkey
                </button>
            </div>
        </form>
    </div>
</template>

<script>
import {
    startAuthentication,
    startRegistration,
    browserSupportsWebAuthn,
} from "@simplewebauthn/browser";

export default {
    name: "LoginPage",

    data() {
        return {
            username: "xx",
            errors: {},

            isBrowserSupported: browserSupportsWebAuthn(),
        };
    },

    mounted() {
        if (!browserSupportsWebAuthn()) {
            alert("WebAuthn is not supported in this browser");
        }
    },

    methods: {
        async submitRegister() {
            this.trackEvent("D5MKGI4J");

            console.log("submitRegister....");

            // Prompt the user to create a passkey
            this.$http
                .post("/auth/registration/options", {
                    username: this.username,
                })
                .then((response) => startRegistration(response.data))
                // Verify the data with the server
                .then((attResp) =>
                    this.$http.post("/auth/registration/verify", attResp)
                )
                .then((verificationResponse) => {
                    console.log("verificationResponse", verificationResponse);
                })
                .catch((error) => {
                    console.error("error", error);
                });
        },

        async submitLogin() {
            this.trackEvent("OHHWLXDF");

            const response = await this.$http.post("/authentication/options", {
                username: this.username,
            });

            const options = response.data;
            if (!options.challenge) {
                // throw new Error('Invalid authentication options received');
                alert("Invalid authentication options received");
                return;
            }

            const attResp = await startAuthentication(options);

            const verificationResponse = await this.$http.post(
                "/authentication/verify",
                attResp
            );

            if (verificationResponse.data?.verified) {
                this.trackEvent("D7T81ZST");

                alert("Authentication successful");
                return window.location.reload();
            }

            alert("Something went wrong verifying the authentication.");
        },

        trackEvent(eventId) {
            if (typeof fathom === "undefined") {
                return;
            }

            console.log("fathom...");

            fathom.trackGoal(eventId, 0);
        },
    },
};
</script>
